import React from 'react'
import { Helmet } from 'react-helmet'

import { graphql } from 'gatsby'
import { CardColumns, Jumbotron } from 'reactstrap'
import {
  Card,
  CardHeader,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  CardFooter,
} from 'reactstrap'

import { dateOptions } from '../helpers'

import Layout from '../components/layout'
export default blogPage

function blogPage({ data, location }) {
  // évite le node.node!!!
  let nodes = data.allMarkdownRemark.edges.map((node) => node.node)

  // On les classe par date décroissante
  nodes.sort((a, b) => {
    const dA = new Date(a.modifiedDate)
    const dB = new Date(b.modifiedDate)
    return dB - dA
  })
  nodes = nodes.slice(0, 6)

  return (
    <Layout location={location} data={data}>
      <Helmet>
        <meta name="robots" content="index, follow" />
        <meta name="msvalidate.01" content="23C4486B3F6CCCF85A73AA582BA8DF9E" />
        <meta
          name="keywords"
          content="blog, lycée, physique, chimie, informatique, isn, nsi, lyceum"
        />
        <meta
          name="description"
          content="Un site open-source pour les lycéens."
        />
        <meta
          name="classification"
          content="lycée blog physique chimie informatique"
        />
        <meta name="author" content="Benjamin Abel" />
      </Helmet>
      <Jumbotron>
        <h1 className="display-3">Derniers articles</h1>
      </Jumbotron>
      <CardColumns>
        {nodes.map((node) => (
          <a href={`/${node.fields.slug}`} className="text-muted" key={node.id}>
            <Card>
              <CardHeader>
                <CardTitle className="text-center">
                  {node.frontmatter.title}
                </CardTitle>
              </CardHeader>
              <CardBody>
                <CardImg
                  top
                  width="100%"
                  src={node.frontmatter.image.childImageSharp.fixed.src}
                  alt="Card image cap"
                />
                <CardText>{node.frontmatter.excerpt}</CardText>
              </CardBody>
              <CardFooter>
                <p className="text-right">
                  <em>
                    Mis à jour le&nbsp;
                    <time dateTime={node.modifiedDate}>
                      {new Date(node.fields.modifiedDate).toLocaleDateString(
                        `fr-FR`,
                        dateOptions
                      )}
                    </time>
                  </em>
                </p>
                <CardText className="text-right">
                  par {node.frontmatter.author}
                </CardText>
              </CardFooter>
            </Card>
          </a>
        ))}
      </CardColumns>
    </Layout>
  )
}

export const query = graphql`
  query BlogQuery {
    __typename
    allMarkdownRemark(filter: { fields: { type: { eq: "blog" } } }) {
      edges {
        node {
          id
          parent {
            id
            ... on File {
              id
              name
            }
          }
          frontmatter {
            tags
            author
            image {
              childImageSharp {
                fixed(width: 400) {
                  # Choose either the fragment including a small base64ed image, a traced placeholder SVG, or one without.
                  ...GatsbyImageSharpFixed
                }
              }
            }
            title
            excerpt
          }
          fields {
            slug
            modifiedDate
          }
        }
      }
    }
  }
`
